#pageRoom{
    header{
        padding: 24px;
        border-bottom: 1px solid #e2e2e2e2;
    

        .content{
        max-width: 1120px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;

            >img{
            max-height: 45px;
            }

            >div{
                display: flex;
                gap: 16px;

                >button{
                    height: 42px;
                    background: transparent;
                    border: 2px solid #835AFD;

                    color: #835AFD;
                    transition: 0.3s;
                }

                >button:hover{
                    background-color: #835AFD;
                    color: #fff;
                    transition: 0.3s;
                }
            }
        }

       
    }

    main{
        max-width: 800px;
        margin: 0 auto;
        
        .roomTitle{
            margin: 32px 0 24px;
            display: flex;
            align-items: center;
            
            h1{
                font-family: 'Poppins', sans-serif;
                font-size: 24px;
                color: #29292e
            }

            span{
                margin-left: 16px;
                background-color: #e559f9;
                border-radius: 1999px;
                padding: 8px 16px;
                color: #fff;
                font-weight: 500;
                font-size: 14px;
            }
        }

        form{
            textarea{
                width: 100%;
                border: 0;
                padding: 16px;
                border-radius: 8px;
                background-color: #fefefe;
                box-shadow: 8 2px 12px rgba(0, 0, 0, 0.04);
                resize: vertical;
                min-height: 130px;
            }

            .formFooter{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-top: 16px;
                
                .userInfo{
                    display: flex;
                    height: 32px;
                    
                    align-items: center;

                    img{
                        border-radius: 9999px;
                        height: 32px;
                        width: 32px;
                    }
                    >span{
                        color: #29292e;
                        font-size: 14px;
                        font-weight: 500;

                        margin-left: 10px;
                    }
                }

                span{
                    font-size: 14px;
                    color: #737380;
                    font-weight: 500;

                    button{
                        background: transparent;
                        border: 0;
                        color: #835AFD;
                        text-decoration: underline;
                        font-size: 14px;
                        font-weight: 500;
                        cursor:pointer;
                    }
                }
            
                Button{
                    height: 32px;
                }
            
            }
        }
    
        .questionList{
            margin-top: 32px;
        }
    }
}