#page-newRoom{
    display:flex;
    height: 100vh;
    align-items: stretch;    

    aside{
        flex:7;
        
        background-color: #835AFD;
        color: #FFFFFF;
        
        display: flex;
        flex-direction: column;
        justify-content: center;

        padding: 280px 88px;
        
        img{
            max-width: 320px;
        }

        strong{
            font: 700 36px "Poppins", sans-serif;
            line-height:42px;
            margin-top:16px
        }

        p{
            font-size:24px;
            line-height: 32px;
            margin-top:16px;
            color: #f8f8f8
        }
    }
    
    main{
        flex:8;
        
        padding: 0 32px;

        display: flex;
        align-items: center;
        justify-content: center;
    }

    .main-content{
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: 320px;
        align-items: stretch;
        text-align: center;

        > img{
            align-self: center;
        }

        h2{
            font-size: 24px;
            margin: 64px 0 24px;
            font-family: "Poppins", sans-serif;
        }

        form{
            input{
                height: 50px;
                border-radius: 8px;
                padding: 0 16px;
                margin-top:16px;
                background: #FFFFFF;
                border: 1px solid #a8a8b3;
            }
            button{
                margin-top: 16px;        
            }

            button, input{
                width: 100%;
            }
        }
        p{
            font-size: 14px;
            color: #737380;
            margin-top: 16px;

            a{
                color:#e559f9;
                margin-left: 6px;
            }
        }
    }
}